.NavigationItem {
  margin: 10px 0;
  box-sizing: border-box;
  display: block;
  width: 100%;
}

.NavigationItem a{
  color: #8f5c2c;
  text-decoration: none;
  width: 100%;
  display: block;
  box-sizing: border-box;
}
.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
  color: #40A4C8;
}

@media (min-width: 500px){
  .NavigationItem {
    margin: 0;
    display: flex;
    height: 100%;
    width: auto;
    align-items: center;
  }
  
  .NavigationItem a{
    text-decoration: none;
    color: white;
    padding: 16px 10px;
    height: 100%;
  }
  .NavigationItem a:hover,
  .NavigationItem a:active,
  .NavigationItem a.active{
    background-color: #8f5c2c;
    border-bottom: 4px solid #40A4C8;
    color: white;
  }
}